import React, { Component } from 'react';

import BasicButton from '../../components/BasicButton';
import {
    UPDATE_SUCCESS,
    CONFIRMED,
} from '../../config/copywriting';

import styles from './index.module.scss';


class UpdateSuccess extends Component {

    closeCurPage = () => {
        window.opener = null;
        window.open('', '_self');
        window.close();
        // this.postMessageToMac('closeWindow','');
        //TODO: close page action
    }

    render() {
        return (
            <div className={styles.updateSuccess}>
                <div className={styles.figure}></div>
                <div className={styles.desc}>{UPDATE_SUCCESS}</div>
                <BasicButton text={CONFIRMED}
                             buttonWrapperClass={styles.confirmBtn}
                             onClick={this.closeCurPage}
                />
            </div>
        )
    }
}

export default UpdateSuccess;